body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


body,h1,h2 {
   overflow: hidden;
}
nav {
   display: flex;
   align-items: center;
   background-color: #333;
}
nav h2 {
  flex: 0.5 1;
  color: white;
  text-align: center;
}
.navBar {
  flex:0.5 1;
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}
.navBar li{
width:100%;
}

@media all and (max-width: 500px) {
  nav {
    flex-direction: column;
  }
  #lastBarLi{
    margin-left: 9%
  }
  .navBar {
    flex-direction: column;
  }
  #sidebar {
    flex:0.5 1;
    min-height: 100%;
    height: auto ;
    height: 100%;
  }
  #allReceiptsComp{
    margin-top: 100%;
    display: flex;
  }

  #checkBoxContainer {
    display:flex none;
  }
  #checkBoxContainer > input{
    float:right;
    position: absolute;
    right:-40px;
  }
}

li a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}
li a:hover {
  color: yellow;
}
li:nth-child(4) a{
  margin-right: 20px;

}
.SignUpForm{
  padding-left: 5%;
  padding-right: 5%;
}
input {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    box-sizing: border-box;
}

input:hover {
  background-color: lightblue;
}
button {
	background: #E27575;
	border: none;
	padding: 10px 25px 10px 25px;
	color: #FFF;
	box-shadow: 1px 1px 5px #B6B6B6;
	border-radius: 3px;
	text-shadow: 1px 1px 1px #9E3F3F;
	cursor: pointer;
}
button:hover{
    background: #CF7A7A
}
#errorSpan {
  visibility: hidden;
  color: red;
  padding-left: 50%;
  display: inline-block;

}
@media all and (max-width: 530px) {
  #errorSpan {
    color: red;
    padding-left: 5%;
  }
  .mainPageBackground {
    background-image: url(https://images.freecreatives.com/wp-content/uploads/2015/03/Huge-Backgrounds-17.jpg);
    background-repeat: no-repeat;
    background-position: center center;
    background-attachment: fixed;
    background-size: cover;
    height:100vh;
  }

  #sidebar{
    overflow: hidden;
    width: 100%;
    border-left: 1px solid black;
    position: absolute;
    right:0;
    margin-top:200%;
    margin-bottom: 30px;
  }

  #receiptDetails {
    visibility: hidden;
    width: 60%;
    border-right: 1px solid black;
    position: absolute;
    left:0;
    margin-top:200%;
  }
  #editReceiptComp{
    width: 60%;
    border-right: 1px solid black;
    position: absolute;
    left:0;
    margin-top:200%;
  }
  #detailsButtons{
    flex-direction: column;
  }
  #receiptDetails{
    flex:0.5 1;
    min-height: 100%;
    height: auto;
    height: 100%;
  }
  #EditReceiptButton{
    flex:0,5 1;
    margin-left: 0%;
  }

  body{
    overflow: auto;
  }
  #errorAlert{
    	width: 70%;
  }
}
#inputCCode {
  width: 30%
}

.ConfirmForm{
  margin-left: 30%;
}
#AddReceipt{
  padding-left: 5%;
  padding-right: 60%;
}
#sidebar{
  width: 20%;
  border-left: 1px solid black;
  position: absolute;
  right:0;
  top:72px;
  height:100%;
  overflow: auto;
}
#receiptDetails {
  padding-left: 2%;
  padding-top:2%;
  visibility: hidden;
  width: 35%;
  border-left: 1px solid black;
  position: absolute;
  right:20%;
  top:72px;
  height:100%;
  overflow: auto;
}

#editReceiptComp {
  padding-left: 2%;
  padding-top:2%;
  padding-right:2%;
  width: 35%;
  border-left: 1px solid black;
  position: absolute;
  right:20%;
  top:72px;
  height:100%;
  overflow: auto;
}

#detailsButtons{
  display: flex;
}
#ClosedDescription{
  flex:0,5 1;
}
#EditReceiptButton{
  flex:0,5 1;
  margin-left: 360%;
}


#imgContainer > img{
  max-width: 100%;
  height: auto;
}
#imgContainer {
    width: 40%;
    min-height: 110px;
    max-height: auto;
    padding-left:27%;
}
.mainHeader {
  text-align: center;
}
#mainMiddle{
  text-align: center;
  margin-top: 25%;
  width: 30%;
  margin-left:39%;
  display:flex;
  color:white;
}
.mainPageBackground {
  background-image: url(https://images.freecreatives.com/wp-content/uploads/2015/03/Huge-Backgrounds-17.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-attachment: fixed;
  background-size: cover;
  height:100vh;
}
#descriptionContainer{
flex:0.75 1;
}
#descriptionContainer > p{
text-align: left;
}
#imgLogoContainer{
  flex:0.25 1;
  width: 10%;
  min-height: 70px;
  max-height: auto;

}
#imgLogoContainer > img{
  max-width: 100%;
  padding-top: 48%;

}
#allReceipts{
  line-height: 1.5em;
  list-style-image: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/4621/treehouse-marker.png);
  padding-bottom: 30px;
}
#allReceipts > li:hover {
  color: #E27575;
  cursor: crosshair;

}
#getStartedButt > a{
  text-decoration: none;
  color:white;
}
#sort{
  flex:0.25 1;
}
#checkBoxContainer {
  display: flex;
}
#expInfo{
  float:right;
  padding-right: 5px;
}
#deleteImgContainer{
  width: 15%;
  min-height: 30px;
  max-height: auto;

}
#deleteImgContainer > img{
max-width: 100%;
}
#deleteReceiptDesc {
  display: inline-block;
}
#EderrorSpan {
  color:red;
  visibility: hidden;
}
#errorAlert{
  position: absolute;
  	top: 70%;
  	left: 60%;
  	width: 30%;
  	height: auto;
  	margin-top: -18%;
  	margin-left: -25%;
  	padding: 30px;
  	background-color: #001f3f;
    color:#7FDBFF;
  	border-radius: 4px;
  	box-shadow: 0 0 50px rgba(black,0.5);
  	z-index: 20;
}
.close-modal {
  text-align: right;
  cursor: pointer;
}
#alertErrorTopHeader{
  font-weight: bold;
  font-size: xx-large;
}
#CreateReceiptHeader{
  margin-left: 17%;
}
#YourReceiptsHeader{
  margin-left: 25%;
}

